.admin_userList {
  flex: 4;
  margin: 20px;
}
.admin_user_list_user {
  display: flex;
  align-items: center;
}
.admin_user_list_img {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 10px;
}
.admin_user_list_edit {
  background-color: #c4c4c4;
  border: none;
  font-size: 12px;
  font-weight: 700;
  padding: 5px 25px;
  border-radius: 4px;
  margin-right: 30px;
}
.admin_user_list_delete {
  color: red;
  cursor: pointer;
}
